import React, { useState, useEffect } from "react";
import ButtonAppBar from "../ButtonAppBar";
import Toggle from "../Toggle";
import Harrypotter from "./HarryPotter";
import MUIDataTable from "mui-datatables";

let nextPage = 0;

function Home(props) {
  const [getdata, setGetData] = useState([]);
  const [auth, setAuth] = useState(false);
  const [status, setStatus] = useState("");
  const [species, setSpecies] = useState("");
  const [gender, setGender] = useState("");

  const columns = [
    {
      name: "name",
      label: "Name",
      options: {
        filter: false,
        sort: true,
      },
    },
    {
      name: "status",
      label: "Status",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "gender",
      label: "gender",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "image",
      label: "image",
      options: {
        filter: false,
        sort: false,
        customBodyRender: (value) => (
          <img
            src={value}
            alt="failed to load"
            width={80}
            height={80}
            style={{ borderRadius: "20px" }}
          />
        ),
      },
    },

    {
      name: "species",
      label: "Species",
      options: {
        filter: true,
        sort: false,
      },
    },
  ];
  const onFilterChange = React.useCallback(
    async (page) => {
      try {
        const getUrlValue = await fetch(
          `https://rickandmortyapi.com/api/character/?page=${page}&gender=${gender}&status=${status}&species=${species}`
        );
        if (!getUrlValue.ok) {
          throw new Error("Network response was not ok");
        }
        const fetchData = await getUrlValue.json();
        setGetData(fetchData.results);
      } catch (error) {
        alert("no data found:" + error);
      }
    },
    [gender, species, status]
  );
  const options = {
    serverSide: true,
    count: 826,
    rowsPerPage: 20,
    rowsPerPageOptions: [20, 50, 100],
    onTableChange: (action, tableState) => {
      if (action === "changePage") {
        nextPage = tableState.page + 1;
        onFilterChange(nextPage);
      }
    },
    onFilterChange: (changedColumn, filterList) => {
      setSpecies(filterList[4]);
      setGender(filterList[2]);
      setStatus(filterList[1]);
    },
  };
  useEffect(() => {
    const fetchDataFromApi = async () => {
      try {
        const geturlValue = await fetch(
          "https://rickandmortyapi.com/api/character"
        );
        const fetchData = await geturlValue.json();
        setGetData(fetchData.results);
      } catch (error) {
        alert("Error fetching data:" + error);
      }
    };
    fetchDataFromApi();
  }, []);

  useEffect(() => {
    onFilterChange();
  }, [onFilterChange]);

  return (
    <>
      <ButtonAppBar />

      <Toggle setauth={setAuth} />

      {auth ? (
        <Harrypotter />
      ) : (
        <div style={{padding:40}}>
        <MUIDataTable columns={columns} data={getdata} options={options} />
        </div>
      )}
    </>
  );
}

export default Home;
