import { Box, Grid } from "@mui/material";
import React from "react";
import { useEffect, useState } from "react";

function Harrypotter() {
  const [HarrypoterData, setHarrypoterData] = useState([]);

  useEffect(() => {
    const fetchDataFromApi = async () => {
      const geturlvalue = await fetch(
        "https://harry-potter-api-en.onrender.com/characters"
      );
      const fetchData = await geturlvalue.json();
      setHarrypoterData(fetchData);
    };
    fetchDataFromApi();
  }, [HarrypoterData]);

  return (
    <Grid container spacing={3} justifyContent="center" alignItems="center">
      {HarrypoterData.map((item) => (
        <Grid item xs={3} key={item.id}>
          <Box  sx={{ flexGrow: 1, border: "10px solid",background:"white",color:"black" }}>
              <p>
              name: {item.nickname}
              <br />
              id: {item.id}
              <br />
              interpretedBy: {item.interpretedBy}
              <br />
              <br />
              <img
              
                src={item.image}
                alt="slow internet"
                height={"200px"}
                width={"200px"}
              />
            </p>
          </Box>
        </Grid>
      ))}
    </Grid>
  );
}

export default Harrypotter;




