import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { useNavigate } from 'react-router-dom';
import { Switch } from '@mui/material';
import {useContext } from 'react';
import ThemeContext from './components/ThemeContext';



export default function ButtonAppBar() {
  
  const {checked, setChecked}=useContext(ThemeContext);

  

  const handleChange = (event) => {
    setChecked(event.target.checked);
  };


  const navigate=useNavigate();
  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static">
        <Toolbar>
        
          
        <Typography variant="h6" component="div" sx={{ flexGrow: 1, alignItems: "center", textAlign: "center" }}>
      <h2>RICK AND MORTY</h2>
    </Typography>
          <Switch
      checked={checked}
      onChange={handleChange}
      inputProps={{ 'aria-label': 'controlled' }}
    />
          <Button color="inherit" onClick={()=>{navigate('/')}}  sx={{background:"red"}} >Log out</Button>
        </Toolbar>
      </AppBar>
    </Box>
  );
}
